import { getElem, getElems, onReady, onScroll, elemIsInView } from "../tools/utilities/helper-functions";

const imageAndTextDouble = getElem('[data-image-and-text-double]')!
const images = getElems('[data-image]', imageAndTextDouble)
images?.length && onScroll(() => {
  images.forEach(image => {
    if(elemIsInView(image)){
      image.classList.add('fade-in')
    }
  });    
})